<template>
  <div id="app">
    <Header/>
    <Main :videos="videos"/>
  </div>
</template>

<script>
// import Header from './components/Header.vue'
import axios from 'axios'

export default {
  // components: { Header },
  name: 'App',
  data: function () {
    return {
      videos: []
    }
  },
  created: function () {
    let _this = this
    axios.get('https://api.apiopen.top/getJoke?page=1&count=10&type=video')
    .then(function(res) {
      _this.videos = res.data.result
    })
  }

}
</script>

